.container-controls {
    color: #FFFFFF;
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 700;

    display: flex;
    justify-content: space-between;
    height: 132px;
    width: 100%;
    background: linear-gradient(180deg, #383535 0%, rgba(133, 130, 130, 0) 100%);
    border-top: 2px solid rgba(0, 0, 0, 0.21);
}

.container-singer {
    width: 10%;
    padding: 24px 0px 0px 32px;
}

.container-singer h1 {
    font-size: 24px;
    margin-bottom: 5px;
}

.container-singer strong {
    display: block;
    margin-top: 8px;
    font-size: 16px;
}

.container-player {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 80%;
}

.container-buttons {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 16px;
}

.container-buttons img {
    cursor:pointer;
    margin-right: 16px;
}


.container-buttons img:hover {
    transform: scale(1.1);
}

.empty {
    width: 10%;
}

.btn-control {
    width: 48px;
    height: 48px;
}

.btn-play-pause {
    width: 64px;
    height: 64px;
}

.container-progress {
    margin-top: 16px;
    margin-bottom: 16px;
    display: flex;
    align-items: center;
    width: 576px;
    color: #CCC3C3;
}

.start {
    margin-right: 8px;
}

.end {
    margin-left: 8px;
}

.container-line {
    width: 100%;
    position: relative;
}

.progress-line {
    border-bottom: 4px solid #CCC3C3;
}

.progress-line-color {
    border-bottom: 4px solid #E5007B;
    position: absolute;
    top: 0;
    left: 0;
}

