* {
    margin: 0;
    padding: 0;
}

.container {
    background: linear-gradient(180deg, #0C0505 0%, #3A3535 47.92%, #5E5858 100%);
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}


.container-main h2 {
    border-bottom: 1px solid #6a6363;
    padding-bottom: 24px;
    margin-bottom: 12px;
    color: #FFFFFF;
    font-family: 'Roboto', sans-serif;
    font-weight: 700;
    font-size: 24px;
    line-height: 28px;
}

.container-main-musics {
    gap: 34px;
    width: 85%;

    display: flex;
    align-items: center;
}
