* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.container-playlist {
    cursor: pointer;
    gap: 37px;
    width: 260px;
    height: 436px;

}

.container-playlist:hover {
    transform: scale(1.05);
}

.container-playlist img {
    width: 260px;
    height: 260px;
    margin-bottom: 8px;
}

.container-playlist h1 {
    color: #FFFFFF;
    font-family: 'Rubik', sans-serif;
    font-weight: 700;
    font-size: 20px;
    margin-bottom: 8px;
}

.container-playlist p {
    color: #CCC3C3;
    font-family: 'Rubik', sans-serif;
    font-weight: 400;
    font-size: 16px;
    text-align: justify;

    width: 260px;
    height: 136px;
}