* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.header {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;

    width: 100vw;
    height: 100px;
    border-bottom: 1px solid #6a6363;
}

.logo img {
    height: 64px;
    width: 292px;
    margin-left: 32px;
}

.welcome {
    color: #FFFFFF;
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    gap: 8px;

    display: flex;
    align-items: center;
    margin-right: 32px;
}

.welcome-image {
    background-image: url('../../assets/cat-profile.jpg');
    background-size: cover;
    border: 2px solid #6A6363;
    border-radius: 50%;
    height: 40px;
    width: 40px;
}